<template>
    <div>
        <!-- Table All Wilayah -->
        <vx-card>
        <vs-table stripe search pagination  max-items="10" :data="wilayah">
            <template slot="header" class="mb-5">
                <h1 class="mr-2">Data Wilayah</h1>
                <vx-tooltip text="Tambah Wilayah" style="display:inline">
                    <vs-button @click="btnAddWilayah" radius icon="create" color="success" type="flat" size="large"></vs-button>
                </vx-tooltip>
            </template>
            <template slot="thead">
                <vs-th sort-key="nama" class="bg-primary text-white">
                Nama
                </vs-th>
                <vs-th sort-key="aksi" width="100px" class="bg-primary text-white text-center">
                <p class="w-full">Aksi</p>
                </vs-th>
            </template>

            <template slot-scope="{data}">
                <vs-tr :key="items.kode" v-for="items in data" >
                    <vs-td :data="items.nama">
                        {{items.nama}}
                    </vs-td>
                    <vs-td :data="items.aksi" class="text-center">
                    <vx-tooltip text="Ubah Wilayah">
                        <vs-button @click="editData(items.kode, items.nama)" class="mr-1" color="warning" type="filled" size="small" icon="create" style="display:inline"></vs-button>
                    </vx-tooltip>
                    <!-- <vs-button @click="deleteData(items.kode)" color="danger" type="filled" size="small" icon="delete" style="display:inline"></vs-button> -->
                    </vs-td>
                </vs-tr>
            </template>
        </vs-table>
        </vx-card>

        <!-- Add Wilayah -->
        <vs-popup class="holamundo"  title="Tambah Wilayah" :active.sync="addNew">
                <div class="vx-row mb-6">
                    <div class="vx-col w-full">
                    <vs-input class="w-full" label="Nama Wilayah" v-validate="'required'" name="form.nama" data-vv-validate-on="blur" v-model="form.nama" />
                    </div>
                </div>
                <div class="vx-row">
                    <div class="vx-col w-full text-right">
                    <vs-button color="success" :disabled="!validateFormAdd" class="mr-3 mb-2" @click=" addData()">Simpan</vs-button>
                    <!-- <vs-button color="warning" type="border" class="mb-2" @click="nama = '' ">Reset</vs-button> -->
                    </div>
                </div>
        </vs-popup>

        <!-- Edit Wilayah -->
        <vs-popup class="holamundo"  title="Edit Wilayah" :active.sync="Edit">
                <div class="vx-row mb-6">
                    <div class="vx-col w-full">
                    <vs-input class="w-full" label="Nama Wilayah" v-validate="'required'" name="form.nama" data-vv-validate-on="blur" v-model="form.nama" />
                    </div>
                </div>
                <div class="vx-row">
                    <div class="vx-col w-full text-right">
                    <vs-button color="success" :disabled="!validateFormEdit" class="mr-3 mb-2" @click="updateData(form.kode)">Simpan</vs-button>
                    <!-- <vs-button color="warning" type="border" class="mb-2" @click="nama = kategori= harga = stock = '' ">Reset</vs-button> -->
                    </div>
                </div>
        </vs-popup>
    </div>
</template>

<script>
export default {
    data() {
        return {
            publicPath: process.env.BASE_URL,
            addNew:false,
            Edit:false,
            wilayah:[],
            form: {
                kode:'',
                nama:'',
            }

        }
    },

    computed: {
        validateFormAdd() {
        return !this.errors.any() && this.form.nama != '';
        },

        validateFormEdit() {
        return !this.errors.any() && this.form.nama != '';
        },
    },

    created() {
       this.loadData();
    },

    methods:{
        btnAddWilayah(){
            this.addNew = true,
            this.form.nama = ''
        },

        loadData(){
            this.$http.get('agen/web/wilayah')
            .then(response => {
                this.wilayah = response.data.message
            })
            .catch(err=>{
                    this.$swal.fire(
                        'Gagal !',
                        err.response.data.message,
                        'error'
                        )
                    })
        },

        addData(){
            this.$vs.loading();
        
            const postData = {
                nama: this.form.nama,
            }

            this.$http.post('agen/web/wilayah/create', postData)
            .then(response => {
                if(response.data.status == "SUKSES"){
                    this.form.nama = '',
                    this.addNew = false

                    this.$swal.fire({
                    title: 'Success',
                    text: response.data.message,
                    icon: 'success',

                    }).then(() => {
                        this.loadData();
                    })
                }else {
                    this.$swal.fire(
                        'Gagal !',
                        response.data.message,
                        'error'
                    )
                }
            })
            .catch(err=>{
                    this.$vs.loading.close()
                    this.$swal.fire(
                        'Gagal !',
                        err.response.data.message,
                        'error'
                        )
                    })
            .finally(() => {this.$vs.loading.close()})
        },

        editData(kode, nama){
            this.Edit = true
            this.form.kode = kode
            this.form.nama = nama 
        },

        updateData(){
            this.$vs.loading();
            const updtData = {
                kode: this.form.kode,
                nama: this.form.nama,
            }
            this.$http.post('agen/web/wilayah/update' , updtData)
            .then(response => {
                if(response.data.status == "SUKSES"){
                    this.form.nama = '',
                    this.Edit = false

                    this.$swal.fire({
                    title: 'Success',
                    text:  response.data.message,
                    icon: 'success',

                    }).then(() => {
                        this.loadData();
                    })
                }else {
                    this.$swal.fire(
                        'Gagal !',
                        response.data.message,
                        'error'
                    )
                }
            })
            .catch(err=>{
                    this.$vs.loading.close()
                    this.$swal.fire(
                        'Gagal !',
                        err.response.data.message,
                        'error'
                        )
                    })
            .finally(() => {this.$vs.loading.close()});
        }
    }

}
</script>

<style lang="stylus">

    .swal2-container 
        z-index: 100000;
        
</style>